.secondSection {
    background-color: #EDF7FA;
    height: 500px;
}
@media (max-width: 1078px) {
    .secondSection {
        background-color: #FFFFFF;
    }
    .entryArticles {
        max-width: fit-content;
    }
    .containers {
        flex-wrap: wrap;
    }
    .entry {
        display: flex;
        flex-wrap: wrap;
        text-align: left;
        margin: 0;
    }
}
.entry {
    display: flex;
    justify-content: space-between;
    margin-left: 148px;
}
.entryPosts {
    font-size: 22px;
    line-height: 60px;
}
.entryArticles {
    display: inline-flex;
    margin-right: 180px;
    font-size: 22px;
    line-height: 60px;
    text-align: right;
}
.containers {
    display: inline-flex;
    flex-wrap: nowrap;
    margin: 69px 148px 32px;
}

.container {
    background-color: #FFFFFF;
    max-width: 418px;
    height: 295px;
    margin-right: 43px;
    box-shadow: 5px -5px 5px 5px rgba(0, 0, 0, .1);
}

.containerTitle {
    font-weight: 700;
    font-size: 26px;
    line-height: 38px;
    width: 374px;
    margin-right: 23px;
    margin-left: 21px;
}
.containerDetails {
    margin-right: 23px;
    margin-left: 21px;
    display: flex;
    width: 374px;
    font-size: 18px;
    line-height: 26px;
}
.containerDate {
    border-right: 1px solid black;
    width: 40%;
}
.containerText{
    width: 60%;
}
.containerPar {
    margin-right: 23px;
    margin-left: 21px;
    text-align: justify;
}