@media (max-width: 1166px) {
    .thirdSection {
        margin-top: 300px;
        width: 100%;
    }
    .work{
        flex-direction: column;
        max-width: fit-content;
    }
    .descriptionPar {
        text-align: center;
    }
}

@media (max-width: 768px) {
    .work{
        display: block;
        align-items: center;
        justify-content: center;
    }
    .picture{
        max-width: 246px;
        margin-left: 0;
        margin-right: 0;
        margin-top: 20px;
        align-items: center;
    }
    .description{
        margin-right: 0;
        margin-left: 0;
        text-align: justify;
    }
}
.work{
    display: flex;
    border-bottom: 1px solid #E0E0E0;
}
.thirdSectionTitle{
    width: 192px;
    margin-top: 22px;
    margin-bottom: 15px;
    margin-left: 148px;
    font-size: 22px;
    line-height: 60px;
    color: #21243D;
}
.picture{
    max-width: 246px;
    margin-left: 146px;
    margin-right: 20px;
    margin-top: 20px;
}
.description{
    margin-right: 148px;
    max-width: 70%;
}
.descriptionTitle{
    font-weight: 700;
    font-size: 30px;
    line-height: 44px;
}
.descriptionPar{
    font-size: 16px;
    line-height: 24px;
}
