.firstSection {
    display: flex;
    flex-wrap: nowrap;
}
.firstSectionText {
    margin-left: 148px;
    margin-right: 206px;
}
.firstSectionTitle {
    font-weight: 700;
    font-size: 44px;
    line-height: 60px;
    margin-left: 0;
    margin-top: 228px;
    max-width: 510px;
}
.firstSectionStatement {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    max-width: 497px;
}
.firstSectionButton {
    background-color: #FF6464;
    border-radius: 5px;
    font-weight: 500;
    font-size: 20px;
    line-height: 29px;
    color: #FFFFFF;
    border: none;
    cursor: pointer;
    margin-top: 47px;
    margin-bottom: 90px;
}
.firstSectionPhoto {
    width: 243px;
    padding-right: 149px;
    border-radius: 50%;
    margin-top: 210px;
}
@media (max-width: 1078px) {
    .firstSection {
        flex-direction: column;
        max-width: 100%;
    }
}
@media (max-width: 780px) {
    .wrapper {
        width: 100%;
    }
    .firstSectionText {
        margin-left: 20px;
        margin-right: 20px;
    }
    .firstSectionTitle{
        text-align: justify;
        font-weight: 400;
        font-size: 22px;
        line-height: 30px;
        margin-left: 0;
        margin-top: 228px;
    }
    .firstSectionStatement {
        text-align: justify;
        font-size: 16px;
        line-height: 20px;
        max-width: 497px;
    }
    .firstSectionPhoto {
        display: none;
    }
}